// Minimal, readable site stylesheet
$color-primary: hsl(0, 0%, 8%);
$color-text: hsl(0, 0%, 12%);
$color-subtext: hsl(0, 0%, 46%);
$color-border: hsl(0, 0%, 80%);
$color-box-background: hsl(0, 0%, 95%);
$color-page-background: hsl(0, 0%, 99%);
$color-accent: #1a73e8;
$border-radius: 6px;
$font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial,
  sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";

html { box-sizing: border-box; font-family: $font-family; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }
*, *:before, *:after { box-sizing: inherit; }

:root {
  --color-page-background: #{$color-page-background};
  --color-box-background: #{$color-box-background};
  --color-border: #{$color-border};
  --color-accent: #{$color-accent};
  --nav-height: 64px;
}

/* Base layout */
body {
  margin: 0;
  padding-top: calc(var(--nav-height) + 0.5rem);
  color: $color-text;
  background: $color-page-background;
  line-height: 1.7;
  font-size: clamp(1rem, 1rem + 0.3vw, 1.06rem);
  -webkit-text-size-adjust: 100%;
}


/* Layout wrapper */
.wrapper { max-width: 85ch; margin: 0 auto; padding: 3vh 4vw; }
main { padding: 0; }

/* Top fixed navigation */
nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: var(--nav-height);
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255,255,255,0.98);
  border-bottom: 1px solid var(--color-border);
  backdrop-filter: blur(4px);
  z-index: 1000;
}
nav a { color: $color-accent; text-decoration: none; font-weight: 600; padding: 0.25rem 0.5rem; border-radius: 4px; }
nav .nav-inner { display:flex; align-items:center; gap:1rem; justify-content:center; }
nav .site-links a { margin-left: 0.6rem; }

@media (max-width:700px) {
  nav { padding: 0.6rem 2vw; }
  nav a { padding: 0.25rem 0.4rem; }
}

/* Typography */
p { margin: 0 0 1.15em; hyphens: auto; }
h1 { font-size: clamp(1.6rem, 1.3rem + 2.5vw, 2.25rem); margin: 0 0 0.4em; font-weight:700; color: $color-primary; }
h2 { font-size: clamp(1.25rem, 1.05rem + 1.5vw, 1.6rem); margin: 1em 0 0.35em; font-weight:600; }
h3 { font-size: clamp(1.05rem, 0.95rem + 1vw, 1.25rem); margin: 0.9em 0 0.3em; font-weight:600; }
h4,h5,h6 { margin:0.9em 0 0.25em; }

ul, ol { margin: 0 0 1.15em 1.25em; padding: 0; }

/* Images, figures */
img { display:block; max-width:100%; height:auto; border-radius: $border-radius; }
figure { margin: 0 0 1.25em; }
figcaption { color: $color-subtext; font-size: 0.95rem; margin-top: 0.5em; }

/* Code and pre */
code { background: $color-box-background; padding: 0.12em 0.32em; border-radius: 4px; font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, "Roboto Mono", monospace; font-size: 0.95em; }
pre { background: $color-box-background; border-radius: 8px; padding: 1em; overflow:auto; margin: 1em 0; }

/* Blockquote */
blockquote { padding: 1.2em; margin: 0 0 1.2em; font-size: 0.98em; background: $color-box-background; border-left: 4px solid $color-accent; color: $color-primary; border-radius: 6px; }

/* Tables and hr */
table { width:100%; border-collapse:collapse; margin: 0 0 1.25em; }
th, td { border:1px solid $color-border; padding:0.5em 0.75em; text-align:left; }
hr { height:1px; background:$color-border; border:0; margin:1.5em 0; }

/* Form and focus */
:focus-visible { outline: 3px solid rgba(255,216,155,0.95); outline-offset: 3px; }

/* Notes grid (kept) */
#notes-entry-container { display:grid; gap:2em; grid-template-areas: "content" "side"; }
@media (min-width:700px) { #notes-entry-container { grid-template-columns: 3fr 1fr; grid-template-areas: "content side"; } }

.backlink-box { background: $color-box-background; padding: 1em; border-radius: $border-radius; border: 1px solid $color-border; }

.invalid-link { color: $color-subtext; cursor: help; background: $color-box-background; padding: 0 0.1em; border-radius: 3px; border: 1px solid $color-border; }
.invalid-link-brackets { color: $color-border; cursor: help; }

/* Links */
a { color: $color-accent; text-decoration: underline; background: transparent; }
a:hover, a:focus { color: $color-primary; text-decoration: underline; background: transparent; }

/* Helpers */
.sr-only { position:absolute!important; width:1px;height:1px;padding:0;margin:-1px;overflow:hidden;clip:rect(0,0,0,0);white-space:nowrap;border:0; }

@media (max-width:700px) {
  .wrapper { padding: 3vh 4vw; }
  nav { gap:0.6rem; }
}

/* Accessibility helpers */
.skip-link {
  position: absolute;
  top: -40px;
  left: 1rem;
  background: var(--color-accent);
  color: white;
  padding: 0.5rem 0.75rem;
  border-radius: 4px;
  z-index: 1000;
  transition: top 0.18s ease;
}
.skip-link:focus { top: 0.75rem; outline: none; }

/* Respect reduced motion preferences */
@media (prefers-reduced-motion: reduce) {
  * { transition: none !important; animation: none !important; }
}
